@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Yellowtail&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

html {
  scroll-behavior: smooth;
}



.navbar {
  background-color: #000;
  width:100%;
  height: 80px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  position: fixed;
  z-index:1;
}

.nav-icons {
  margin-right: 5px;
}

.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  background: none;
  z-index:1;
}

.brand {
 display: none;
}

.nav-menu {
  background-color: #000;
  width:250px;
  height:100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  position:fixed;
  top:0;
  left:-100%;
  transition: 850ms;
}

.nav-menu.active {
  left:0;
  transition: 350ms;
}


.nav-text {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
  color: #f5f5f5;
}

.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 95%;
  height:100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 16px;
  border-radius: 4px;

}

.nav-text a:hover {
  background-color: #1a83ff;
}


.nav-menu-items {
  width: 100%;
}

.navbar-toggle {
  background-color: #000;
  width: 80%;
  height: 80px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
}

span {
  margin-left: 16px;
}


@media only screen and (min-width: 720px) {
  .navbar {
    position: relative;
    background-color: #fff;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
  }

  .menu-bars {
   display: none;
  }

  .nav-menu {
    position: absolute;
    border-radius: 8px;
    background-color: #000;
    width:90%;
    height:80px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    top:15px;
    left: 0;
    -webkit-transform: translate(5%);
            transform: translate(5%);
    padding: 0 40px;
    transition: none;
    position: fixed;
    z-index:2;
  }

  .nav-menu.active{
    transition:none;
  }

  .nav-menu-items {
    width: unset;
    display: -webkit-flex;
    display: flex;
  }

  .brand {
    display: block;
    color:#f5f5f5;
    font-weight: 100;
    border-right: 1px solid #f5f5f5;
    padding-right: 10px;
  }

  .navbar-toggle {
    background-color: #000;
    width: 100%;
    height: unset;
    display: unset;
    -webkit-justify-content: unset;
            justify-content: unset;
    -webkit-align-items: unset;
            align-items: unset;
  }

  span {
    margin-left: 16px;
  }


}
.icons {  
  display: none;
}

.tag {
  display: none;
}

.main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 90vh;
} 


.name {
   font-family: 'Yellowtail', cursive;
   font-size: 2.6rem;
 }

 .title {
   font-family: 'Lato', sans-serif;
   font-size: 1.7rem;
 }

 @media only screen and (max-width: 370px) {
  .name {
    font-size: 2.3rem;
  }
 }



 /* Projects */

 .project-heading {
  font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
   text-align: center;
   font-size: 3rem;
   font-weight: bold;
   color:#555;
   margin-bottom: 40px;
 }


 .projects {
  width:80%;
  margin:0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  grid-gap: 5px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 10px 0;
}

.project-card {
  width: 240px;
  margin: 0 auto;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;  
  transition: -webkit-transform 250ms ease-in;  
  transition: transform 250ms ease-in;  
  transition: transform 250ms ease-in, -webkit-transform 250ms ease-in;
  margin-top: 10px;
}

.project-card:hover{
  -webkit-transform: translate(10px);
          transform: translate(10px);
}

.img {
 width:240px;
 height:160px;
 border-radius: 10px 10px 0 0;
}

.img:hover {
  opacity: 0.9;
}


.project-title {
  font-family: 'Lato', sans-serif;
  margin-bottom: 10px;
  text-align: center;
  font-size: 1.5rem;
}


@media only screen and (max-width: 720px) {
  .icons {
    display: -webkit-flex;
    display: flex;
   
  }

  .icon {
   color:#555;
   font-size: 3rem;
   padding-right: 10px;
  }

 
}  



 @media only screen and (min-width: 720px) {

  .main {
    position: relative;
  }

  .tag {
    display: block;
    font-family: 'Lato', sans-serif;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    text-align: center;
    background-color: #0c0c0c;
    color:#fff;
    font-size: 1.2rem;
    margin-top: 20px;
    padding: 10px 5px;
  }

  .name {
    font-size: 4rem;
  }
 
  .title {
    font-size: 3rem;
  }

   .icons {  
    background-color: aquamarine;
    color:#0c0c0c;
    position: absolute;
    width: 0; 
    font-size: 1.8rem; 
    display: -webkit-flex; 
    display: flex; 
    -webkit-flex-direction: column; 
            flex-direction: column;
    position: fixed;
    left:50px;
   }  

  .icon {
    margin: 20px 50px;
    color:#0c0c0c;
    transition: -webkit-transform 250ms ease-in;
    transition: transform 250ms ease-in;
    transition: transform 250ms ease-in, -webkit-transform 250ms ease-in;
  }

  .icon:hover {
    color:#1a83ff;
    -webkit-transform:translateY(-5px);
            transform:translateY(-5px);
  }
  
 }  

 @media only screen and (max-width: 1100px) {
  
  .icons {  
    left:40px;
   } 
  

  .icon {
    margin: 20px 0;
  }

}




 @media only screen and (min-width: 920px) {
  .name {
    font-size: 6rem;
  }
 
  .title {
    font-size: 3.5rem;
  }
}  



/* About */

.about-section {
  height: 100vh;
  width:70%;
  -webkit-justify-content: center;
          justify-content: center;
  display: -webkit-flex;
  display: flex;
  margin:0 auto;
  -webkit-align-items: center;
          align-items: center;
}

.about {
  margin-left: 50px;
}

.about-heading {
  font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  color:#555;
  font-size: 3rem;
  font-weight: bold;
}

.about-info {
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-size: 1.5rem;
}


@media only screen and (max-width: 920px) {
  .about-section {
    height: 100vh;
    width:75%;
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  
  .about {
    margin-left: 0;
    margin-top:40px;
  }
  
  .about-heading {
    font-weight: 100;
    font-size: 2.5rem;
    font-weight: bold;
  }
  
  .about-info {
    font-size: 1.2rem;
    text-align: center;
  }
}

